@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400&display=swap);
.Products {
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.Products-banner {
  margin-top: -5vw;
  width: 100vw;
  box-shadow: 1px 8px 21px 1px #000000;
}

.Products-intro {
  width: 100vw;
  height: 50vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.Products-company-background {
  width: 100vw;
  height: 30vw;
}

.sony {
  background-color: #1e3da1;
}

.xbox {
  background-color: #23a11e;
}

.nintendo {
  background-color: #bd1f1f;
}

.Products-xbox {
  margin-top: 20vw;
}

.Products-nintendo {
  margin-top: 20vw;
}

.company-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.Products-company-description {
  margin-top: 20vw;
  margin-bottom: 10vw;
  font-size: 2vw;
  width: 80vw;
  text-align: center;
}

.Products-intro h3 {
  font-size: 3vw;
  color: black;
}

.Products-intro p {
  font-size: 2vw;
  color: black;
}

.Products-items {
  width: 100vw;
  justify-content: center;
  grid-template-columns: 28vw 28vw 28vw;
  grid-gap: 2vw;
  display: grid;
  margin-top: -15vw;
}
.Products-item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  box-shadow: 9px 11px 25px 0px black;
  border-radius: 1vw;
  width: 25vw;
  height: 30vw;
  background-color: #fff;
  position: relative;
}
.Products-item-info {
  width: 90%;
}
.Products-item-info h2 {
  font-size: 1.2vw;
  font-weight: bold;
  display: flex;
  justify-content: space-between;
}
.Products-item-info h2 span {
  color: #33b13a;
}
.Products-item-info p {
  font-size: 1vw;
  font-weight: 300;
}
.Products-item img {
  width: 100%;
  height: 15vw;
  border-radius: 5px 5px 0 0;
  object-fit: contain;
}
.Products-item button {
  position: absolute;
  top: 28vw;
  left: 17vw;
  color: black;
  box-shadow: 9px 11px 25px 0px black;
  border-color: transparent;
  width: 40%;
  padding: 1vw;
  border-radius: 1vw;
  outline: none;
  cursor: pointer;
  font-size: 1vw;
  font-weight: 300;
  transition: 0.3s ease;
}
.Products-item button:hover {
  background-color: lightgray;
  width: 50%;
}

@media screen and (max-width: 1024px) {
  .Products-banner {
    display: none;
  }

  .Products-intro {
    margin-top: 25vmin;
    margin-bottom: 50vmin;
  }

  .Products-intro h3 {
    font-size: 6.5vmin;
    width: 90vw;
    text-align: center;
  }

  .Products-intro p {
    font-size: 5vmin;
    width: 90vw;
    text-align: center;
  }

  .Products-company-background {
    height: 500vmin;
  }

  .Products-company-description {
    font-size: 5vmin;
    margin-top: 80vmin;
    margin-bottom: 80vmin;
  }

  .Products-items {
    grid-template-columns: 90vmin;
    grid-template-rows: 100vmin 100vmin 100vmin;
    grid-gap: 40vmin;
    margin-top: 0;
    position: absolute;
    margin-top: -110vmin;
  }

  .Products-item {
    width: 90vmin;
    height: 120vmin;
    justify-content: flex-start;
  }

  .Products-item img {
    height: 75vmin;
    width: 100%;
    margin-top: 5vmin;
  }

  .Products-item-info {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .Products-item-info h2 {
    font-size: 5vmin;
    width: 95%;
  }

  .Products-item-info p {
    font-size: 3.5vmin;
  }

  .Products-item button {
    padding: 5vmin;
    width: 50%;
    font-size: 3vmin;
    top: 95%;
    left: 25%;
  }
}

.Checkout-container {
  width: 90vw;
  grid-template-columns: 3fr 1fr;
  grid-gap: 2vw;
  grid-row-gap: 1.5vw;
  display: grid;
}

.Checkout-title {
  font-size: 1.5vw;
}

.Checkout-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 10px 0 0 0;
}
.Checkout-item .fas {
  margin: 0 0 0 10px;
  color: rgba(0, 0, 0, 0.4);
  cursor: pointer;
}
.Checkout-item button {
  background-color: transparent;
  border: none;
  outline: none;
}
.Checkout-element {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  border-bottom: 0.1vw solid #eee;
}
.Checkout-element h4 {
  font-size: 1vw;
  margin: 0;
}

.Checkout-element span {
  font-size: 1vw;
}

.Checkout-sidebar button {
  box-shadow: inset 0px 1px 0px 0px #bee2f9;
  background: linear-gradient(to bottom, #63b8ee 5%, #468ccf 100%);
  background-color: #63b8ee;
  border-radius: 1vw;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  font-family: Arial;
  font-size: 1.2vw;
  font-weight: bold;
  text-decoration: none;
  width: 80%;
  height: 3vw;
  display: block;
  outline: none;
}

a {
  text-decoration: none;
}

@media screen and (max-width: 1024px) {
  .Checkout-container {
    grid-template-columns: 100%;
    grid-gap: 10vmin;
    grid-row-gap: 10vmin;
  }

  .Checkout-title {
    font-size: 4vmin;
  }

  .Checkout-item {
    margin: 10vmin 0 0 0;
  }

  .Checkout-element {
    border-bottom: 1vmin solid #eee;
  }
  .Checkout-element h4 {
    font-size: 3vmin;
  }

  .Checkout-element span {
    font-size: 3vmin;
  }

  .Checkout-sidebar button {
    font-size: 3vmin;
    width: 40%;
    height: 15vmin;
  }
}

.Information {
  width: 100vw;
  margin-bottom: 3vw;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.Information-content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 80vw;
}

.Information-head h2 {
  font-size: 1.5vw;
}

.Information-buttons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 80vw;
}
.Information-sidebar {
  margin: 1vw 0 0 0;
}

.Information-sidebar h3 {
  font-size: 1vw;
}

.Information-back {
  margin: 1vw 0 0 0;
  font-size: 1.2vw;
}
.Information-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.Information-item .fas {
  margin: 0 0 0 1vw;
  color: rgba(0, 0, 0, 0.4);
  cursor: pointer;
}
.Information-item button {
  background-color: transparent;
  border: none;
  outline: none;
}
.Information-element {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  border-bottom: 0.1vw solid #eee;
}
.Information-element h4 {
  margin: 0;
  font-size: 1vw;
}
h2 {
  margin: 0 0 1vw 0;
  padding: 0;
  font-size: 1vw;
}
h3 {
  font-size: 1.2vw;
  margin: 0 0 8px 0;
  padding: 0;
}

.Information-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.Information-form form {
  width: 95%;
}

input {
  background-color: #fff;
  background-image: none;
  border-radius: 0.5vw;
  border: 0.1vw solid #d9d9d9;
  color: rgba(0, 0, 0, 0.75);
  display: inline-block;
  font-feature-settings: 'tnum';
  font-size: 0.8vw;
  padding-left: 1vw;
  height: 2vw;
  width: 95%;
  list-style: none;
  margin: 0 0 1vw 0;
  outline: 0;
  box-sizing: border-box;
}
.Information-buttons {
  margin: 10px 0 0;
}
.Information button {
  box-shadow: inset 0px 1px 0px 0px #bee2f9;
  background: linear-gradient(to bottom, #63b8ee 5%, #468ccf 100%);
  background-color: #63b8ee;
  border-radius: 0.5vw;
  border: 0.1vw solid #3866a3;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  color: #14396a;
  font-family: Arial;
  font-size: 1vw;
  font-weight: bold;
  text-decoration: none;
  width: 10vw;
  height: 3vw;
  outline: none;
}

@media screen and (max-width: 1024px) {
  .Information-content {
    margin-top: 20vw;
  }

  .Information-head h2 {
    font-size: 4vmin;
    margin-bottom: 5vmin;
  }

  .Information-buttons {
    width: 80vw;
  }
  .Information-sidebar {
  }

  .Information-sidebar h3 {
    font-size: 3vmin;
  }

  .Information-back {
    margin: 1vw 0 0 0;
    font-size: 3.5vmin;
  }
  .Information-element {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    border-bottom: 0.1vw solid #eee;
  }
  .Information-element h4 {
    margin: 0;
    font-size: 2vmin;
  }
  h2 {
    margin: 0 0 1vw 0;
    padding: 0;
    font-size: 2vmin;
  }
  h3 {
    font-size: 2vmin;
    margin: 0 0 8px 0;
    padding: 0;
  }

  .Information-form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .Information-form form {
    width: 95%;
  }

  input {
    border-radius: 10vmin;
    font-size: 3.5vmin;
    padding-left: 10vmin;
    height: 10vmin;
    width: 95%;
    margin: 0 0 10vmin 0;
  }
  .Information-buttons {
    margin: 10px 0 0;
  }
  .Information button {
    border: 0.1vw solid #3866a3;
    border-radius: 10vmin;
    font-size: 2.5vmin;
    width: 20vmin;
    height: 10vmin;
  }
}

.Payment {
  grid-template-columns: 3fr 1fr;
  grid-gap: 2vw;
  grid-row-gap: 1.5vw;
  display: grid;
  margin-top: 10vw;
  width: 100vw;
  height: 70vh;
  justify-content: center;
}

.Payment-content {
  margin: 1vw;
  width: 90vw;
}

.Payment-content h3 {
  font-size: 1.5vw;
}

.Payment-back {
  margin: 1vw 0 0 0;
}
.Payment-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.Payment-item .fas {
  margin: 0 0 0 1vw;
  color: rgba(0, 0, 0, 0.4);
  cursor: pointer;
}

.Payment-text {
  font-size: 1vw;
}

.Payment-item button {
  background-color: transparent;
  border: none;
  outline: none;
}
.Payment-element {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  border-bottom: 1px solid #eee;
  margin-bottom: 1.5vw;
}
.Payment-element h4 {
  margin: 0;
}

.Payment-error {
  width: 30vw;
  background-color: #dc6565;
  border: 0.1vw solid #ba2626;
  border-radius: 1vw;
}

.Payment-error p {
  color: #ba2626;
  font-size: 1vw;
  margin: 1vw;
}

@media screen and (max-width: 1024px) {
  .Payment {
    grid-template-columns: 3fr 1fr;
    grid-gap: 2vw;
    grid-row-gap: 1.5vw;
    display: grid;
    margin-top: 10vw;
    width: 100vw;
    height: auto;
    justify-content: center;
  }

  .Payment-content {
    margin: 1vw;
    width: 90vw;
  }

  .Payment-content h3 {
    font-size: 3vmin;
  }

  .Payment-text {
    font-size: 3vmin;
  }
  .Payment-element h4 {
    margin: 0;
    font-size: 3vmin;
  }
  .Payment-error p {
    color: #ba2626;
    font-size: 3vmin;
    margin: 1vw;
  }
}

.Success {
  margin: 0 0 4vw 0;
}
.Success-map {
  margin: 1vw 0 0 0;
}

.Success-text {
  font-size: 2vw;
}

@media screen and (max-width: 1024px) {
  .Success-content {
    margin-top: 10vw;
  }
  .Success-text {
    font-size: 5vmin;
  }
}

.Footer {
  width: 100vw;
  height: 7vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Footer-container {
  display: flex;
  justify-content: space-between;
  width: 95vw;
  font-size: 1.1vw;
}

@media screen and (max-width: 1024px) {
  .Footer {
    display: none;
  }

  .Footer-container {
    font-size: 3vmin;
  }
}

.Header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: black;
  position: fixed;
  width: 100vw;
  height: 5vw;
  z-index: 10;
}

h1 {
  font-size: 1.4vw;
  font-weight: 100;
  margin-left: 2vw;
}

.Header-checkout {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  justify-items: center;
  width: 5vw;
  margin: 3vw;
}
.Header-alert {
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  font-size: 0.8vw;
  width: 1vw;
  height: 1vw;
  position: absolute;
  margin-left: 1vw;
  margin-top: 1.3vw;
  background-color: red;
  border-radius: 50%;
}
.Header a {
  text-decoration: none;
  color: white;
}

@media screen and (max-width: 1024px) {
  .Header {
    height: 15vmin;
  }

  .Header h1 {
    font-size: 4vmin;
    margin-left: 3vw;
  }

  .Header-checkout {
    margin-right: 3vw;
  }

  .Header-alert {
    font-size: 2vmin;
    width: 3vmin;
    height: 3vmin;
    margin-left: 4vmin;
    margin-top: 4vmin;
  }
}

body {
  margin: 0;
  padding: 0;
  color: #3c484e;
  font-family: 'Open Sans', sans-serif;
  overflow-x: hidden;
}

.Main {
  display: grid;
  justify-content: center;
}

.section {
  margin-top: 10vw;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media screen and (min-width: 1024px) {
  .section {
    height: 70vh;
  }
}

@media screen and (max-width: 1024px) {
  .section {
    margin-bottom: 35vmin;
  }
}

